import React from "react";
import {Button, Card, CardBody, CardHeader, Form, FormFeedback, FormGroup, FormInput} from "shards-react";

const FormValidation = () => (
    <Card small className="mb-3">
        <CardHeader className="border-bottom">
            <h3 className="m-0">Login</h3>
        </CardHeader>
        <CardBody>
            <Form>
                <FormGroup>
                    <FormInput size="lg" placeholder="Email" required invalid/>
                    <FormFeedback>The email is already in use.</FormFeedback>
                </FormGroup>
                <FormGroup>
                    <FormInput size="lg" placeholder="Email" required invalid/>
                    <FormFeedback>The email is already in use.</FormFeedback>
                </FormGroup>
                <Button theme="primary" className="mb-2 mr-1">
                    Register
                </Button>
            </Form>
        </CardBody>
    </Card>
);

export default FormValidation;
